import WxValidate from '../../utils/WxValidate.js';
import {
	LOGIN
} from "../../utils/api.js"
export default {
	data() {
		return {
			topItem: [{
				title: "平台信息"
			}, {
				title: "服务介绍"
			}, {
				title: "产品特点"
			}],
			mobileCode: "获取验证码",
			time: 60,
			mobileTip: "",
			inputValue: "",
			smsTip: "",
			mobile: "",
			smsCode: ""
		}
	},
	onLoad() {
		this.initValidate();
		this.getCookie("AI-Chat-Token");
		this.getUrlToken();
	},
	methods: {
		// 规则校验
		initValidate() {
			// 规则
			const rules = {
				mobile: {
					required: true
				},
				smsCode: {
					required: true
				}
			}
			// 提示
			const messages = {
				mobile: {
					required: "请输入手机号！"
				},
				smsCode: {
					required: "请输入验证码"
				}
			}
			this.WxValidate = new WxValidate(rules, messages)
		},
		// 获取验证码
		getMobileCode() {
			this.time = 60
			this.timer()
		},
		timer() {
			if (this.time > 0) {
				this.time--;
				this.mobileCode = "还剩" + this.time + "s"
				setTimeout(this.timer, 1000);
			} else {
				this.time = 0;
				this.mobileCode = "获取验证码";
			}
		},
		// 登录
		handleLogin(e) {
			const params = e.detail.value
			if (!this.WxValidate.checkForm(params)) {
				const error = this.WxValidate.errorList[0]
				// 可以自定义提示
				uni.showToast({
					title: `${error.msg}`,
					icon: 'none'
				});
				return false
			} else {
				console.log("获取的token",this.inputValue)
				// uni.request({
				// 	url: LOGIN,
				// 	data:params,
				// 	method: "POST",
				// 	header:{"AI-Chat-Token":this.inputValue},
				// 	success: (res) => {
				// 		console.log(res)
				// 	}
				// })

				this.$request.url_request(LOGIN, params, "POST", res=>{
					
				}, err=>{
					
				});
				
			}
		},
		// 获取cookies
		getCookie(name) {
			var strcookie = document.cookie; //获取cookie字符串
			var arrcookie = strcookie.split("; "); //分割
			// console.log(arrcookie)
			//遍历匹配
			for (var i = 0; i < arrcookie.length; i++) {
				var arr = arrcookie[i].split("=");
				if (arr[0] == name) {
					this.inputValue = arr[1]
					console.log(this.inputValue)
					return arr[1];
				}
			}
			return "";
		},
		// 获取当前保存token
		getUrlToken() {
			var url = window.location.href
			var urlcode = url.split("?")[1]
			if (urlcode !== undefined) {
				var urlToken = urlcode.split("=")[1]
				this.inputValue = urlToken;
			}
			uni.getStorage({
				key: "myToken",
				data: urlToken,
			})
		},
		// 收起键盘
		pickupKeyboard(){
			uni.pageScrollTo({
				scrollTop:0,
				duration:0
			})
		}
	}
}
